<template>
    <div class='yimin'>
        <h1 style="display:none!important;">{{metaItemName}}</h1>
        <h></h>
        <div class='yiminMain'>
            <div class='ban' id='yiminB'></div>
           <div class='yiminDetBox'>
               <div class='w1200 posiRle' v-for='item in itemInfoArr'>
                   <div class='yiminDTop shadow'>
                       <div class='fl lunbo'>
                           <!-- banner -->
                           <el-carousel :interval="5000" arrow="always" v-if='fileList.length>1'>
                                <el-carousel-item  v-for='(b,index) in fileList' >
                                 <img :src='b.filePath'/>
                                </el-carousel-item>
                            </el-carousel>
                            <img :src='b.filePath' v-for='(b,index) in fileList' v-if='fileList.length==1'/>
                       </div>
                       <div class='fl lunText'>
                           <p class='tit'>{{item.itemTitle}}</p>
                           <p class='intr'>{{item.lightspot}}</p>
                          <p class='type'><span class='fl'>{{item.certType}}</span><span class='fr'>{{item.complexity}}</span></p>
                          <p class='type'><span class='fl'>{{item.liveRequire}}</span><span class='fr'>{{item.cycle}}</span></p>
                          <p class='pric' style='margin-bottom:10px;'>&nbsp;&nbsp;<span>{{item.amountMaxSum}}<i>&nbsp;</i></span></p>
                          <p class='pric' style='margin-top:0;color:#666;'>{{item.foreignCurrency}}</p>
                          <p class='yuyueP'><span class='yuyueBtn fl btnHoverZ' @click='toAppont()'></span><span class='fl tel'><img src='./img/appTel.png'/>010-53520719</span></p>
                       </div>
                   </div>
                     <div style='clear:both;'></div>
                   <div class='yiminDBottom shadow'>
                       <ul class='maoUl'>
                           <a><li class='active' id='nav1' mao='Project_intr'><span></span></li></a>
                           <a><li class='' id='nav2' mao='apply_cond'><span></span></li></a>
                           <a><li class='' id='nav3' mao='cost_det'><span></span></li></a>
                           <a v-if='itdFileNum>0'><li class='' id='nav4' mao='file' style="position:relative;"><span></span><i class='fileDtishi'></i></li></a>
                           <span class='yuyueBtn fr btnHoverZ' @click='toAppont()'></span>
                       </ul>
                       <div id=''>
                            <!--  -->
                            <span id='Project_intr' style='position:relative;top:-240px;opacity: 0;font-size:10px;' class='nav1'>1</span>
                            <ul class='detailUl'>
                                <li><p class='nor' v-html='item.describe'></p></li>
                                <li>
                                    <p class='tit'></p>
                                    <p class='nor' v-html='item.superiority'></p>
                                </li>
                            </ul>
                       </div>
                   </div>
                   <div id='' class='shadow'>
                            <!--  -->
                             <span id='apply_cond' style='position:relative;top:-120px;opacity: 0;font-size:10px;'class='nav2'>1</span>
                            <ul class='detailUl'>
                                <li class='fenLi'>
                                    <p class='tit'></p>
                                     <div class='nor' v-html='item.applyCondition'></div>
                                </li>
                                <li class='fenLi'>
                                    <p class='tit'></p>
                                     <div class='nor' v-html='item.applyProcess'>
                                     </div>
                                </li>
                            </ul>
                    </div>
                    <div id='' class='shadow'>
                        <!--  -->
                        <span id='cost_det' style='position:relative;top:-120px;opacity: 0;font-size:10px;' class='nav3'>1</span>
                        <ul class='detailUl'>
                            <li class='fenLi'>
                                <p class='tit'></p>
                                <p class='nor' v-html='item.totalCost'></p>
                            </li>
                        </ul>
                    </div>
                      <div id='' class='shadow' v-if='itdFileNum>0'>
                        <!--  -->
                        <span id='file' style='position:relative;top:-120px;opacity: 0;font-size:10px;' class='nav4'>1</span>
                        <ul class='detailUl'>
                            <li class='fenLi' v-for="(item,index) in itdFileList">
                                <p class='tit'></p>
                                <img src='./img/file.png' class='fileImg'/>
                                <p class='fileTit'>{{item.fileName}}</p>
                                <a :href='item.filePath'><button class='fileBtn'></button></a>
                                <p class='fileText'></p>
                            </li>
                        </ul>
                    </div>

               </div>
           </div>
        </div>
        <sidebar></sidebar>
        <appointment ref='c1'></appointment>
        <f></f>
    </div>
</template>
<script>
import h from '@/components/header/header.vue'
import f from '@/components/footer/footer.vue'
import sidebar from '../../components/sidebar/sidebar.vue'
import appointment from '../../components/appointment/appointment.vue'
import axios from 'axios'
export default {
    name:'yimin',
     metaInfo:function() {
         return{
              title: this.metaItemName+'__',
              meta: [{                 // set meta
                    name: 'description',
                    content: ''
                },{                 // set meta
                    name: 'keyWords',
                    content: ''
                }]
         }
       
    },
    data:function(){
        return{
             id:'',//Id
             nationCode:'',//code
            itemInfoArr:[],
            fileList:[],
            proId:'',//Id
            name:'',
            itdFileNum:'',
            itdFileList:'',
            ifShow:true,
            loading:this.$loading({
                lock: true,
                text: '...',
                spinner: 'el-icon-loading loading',
                background:'rgba(0, 0, 0, 0.7)',
                }),
            metaItemName:'',
           
            }
        
    },
    components:{sidebar,appointment,f,h},
    methods:{
          getInfo:function(id){
            var that=this;
            var obj={"FLAG":"1","ITEM_ID":id}
            axios.post('/trpch/trcrm/item/queryItemInfo',obj)
            .then(res=>{
                var data=res.data;
                console.log(data);
                if(data.isSuccess==1){
                   var itemInfo=data.itemInfo;
                   that.metaItemName=itemInfo.itemTitle;
                   that.itemInfoArr.push(itemInfo);
                    that.fileList=data.fileList;
                    that.proId=itemInfo.proId;
                    that.name=itemInfo.itemTitle;
                    that.nationCode=itemInfo.nationCode;
                    //
                    that.itdFileNum=data.itdFileNum;
                    that.itdFileList=data.itdFileList;
                   
                     that.loading.close();
                    setTimeout(function(){
                       var detH=$('.posiRle').height()-250;
                       $('.yiminDetBox').height(detH);
                   },500)
                }     
            })
        },
        toAppont:function(){
            var that=this;
            that.$refs.c1.toShow(that.name,that.id,that.ifShow,true,that.nationCode);
        }
        
    },
    created:function(){
        var that=this;
        that.id=that.$route.query.id;
        if(!that.id==false){
           that.getInfo(that.id)//
        }
    }
}
</script>
<style>
    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }
    
    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
     #yiminB{
        background-image: url(img/yiminbanner.jpg);
    } 
    
</style>




