<template>
    <div class='yimin'>
        <h1 style="display:none!important;">{{metaItemName}}</h1>

        <div class='cover' v-show="vCover" @click="closeCover()">
             <el-carousel :interval="5000" arrow="always" v-if='buildlist.length>1'>
                <el-carousel-item  v-for='b in buildlist'>
                    <img :src='b.httpPath' />
                </el-carousel-item>
            </el-carousel>
        </div>

        <h></h>
        <div class='yiminMain'>
            <div class='ban' id='haiWB'></div>
           <div class='yiminDetBox'>
               <div class='w1200 posiRle' v-for='item in itemInfoArr'>
                   <div class='yiminDTop shadow'>
                       <div class='fl lunbo'>
                           <!-- banner -->
                          <el-carousel :interval="5000" arrow="always" v-if='fileList.length>1'>
                                <el-carousel-item  v-for='b in fileList'>
                                 <img :src='b.filePath'/>
                                </el-carousel-item>
                            </el-carousel>
                          <img :src='b.filePath' v-for='(b,index) in fileList' v-if='fileList.length==1'/>
                       </div>
                       <div class='fl lunText'>
                           <p class='tit'>{{item.itemTitle}}</p>
                           <p class='intr'>{{item.lightspot}}</p>
                          <p class='type'><span class='fl'>{{item.returnRate}}%</span><span class='fr'>{{item.paymentRate}}%</span></p>
                          <p class='type'><span class='fl'>{{item.unitCost}}</span><span class='fr'>{{item.structurArea}}</span></p>
                          <p class='pric' style='margin-bottom:14px;'>&nbsp;&nbsp;<span>{{item.amountSum}}<i>&nbsp;</i></span><span style='font-size:14px;color:#666;margin-left:21px;font-weight:100;'><img src='./img/ditu.png' style='vertical-align: -2px;'/>&nbsp;&nbsp;{{item.nation}}&nbsp;&nbsp;{{item.cityName}}</span></p>
                          <p style='font-size:14px;font-weight:400;color:rgba(102,102,102,1);margin-bottom:21px;'>{{item.foreignCurrency}}</p>
                          <p class='yuyueP'><span class='yuyueBtn fl btnHoverZ' @click='toAppont()'></span><span class='fl tel'><img src='./img/appTel.png'/>010-53520719</span></p>
                       </div>
                   </div>
                     <div style='clear:both;'></div>
                   <div class='yiminDBottom shadow'>
                       <ul class='maoUl'>
                          <a><li class='active' id='nav1' mao='fangyuan'><span></span></li></a>
                           <a><li class='' id='nav2' mao='huxing'><span></span></li></a>
                           <a><li class='' id='nav3' mao='zhoubian'><span></span></li></a>
                           <a><li class='' id='nav4' mao='goufang'><span></span></li></a>
                           <a v-if='itdFileNum>0'><li class='' id='nav5' mao='file' style="position:relative;"><span></span><i class='fileDtishi'></i></li></a>
                           <span class='yuyueBtn fr btnHoverZ' @click='toAppont()'></span>
                       </ul>
                       <div id='Project_intr' style='padding:30px;box-sizing:border-box;'>
                            <!--  -->
                             <span id='fangyuan' style='position:relative;top:-270px;opacity:0;font-size:10px;' class='nav1'>1</span>
                            <ul class='zidu'>
                                <div style='clear:both;'></div>
                                <li>
                                    <div class='fl leiH leiHl'><span class='nameH'></span><span class='nameT'>{{item.realEstate}}</span></div>
                                    <div class='fl leiH leiHr'><span class='nameH'></span><span class='nameT'>{{item.adress}}</span></div>
                                </li>
                                <div style="clear:both"></div>
                                 <li>
                                    <div class='fl leiH leiHl'><span class='nameH'></span><span class='nameT'>{{item.equityDeclare}}</span></div>
                                    <div class='fl leiH leiHr'><span class='nameH'></span><span class='nameT'>{{item.payNorm}}</span></div>
                                </li>
                                <div style="clear:both"></div>
                                 <li>
                                    <div class='fl leiH leiHl'><span class='nameH'></span><span class='nameT'>{{item.developers}}</span></div>
                                    <div class='fl leiH leiHr'><span class='nameH'></span><span class='nameT'>{{subTime(item.payDate)}}</span></div>
                                </li>
                                <div style="clear:both"></div>
                                <li>
                                    <div class='fl leiH leiHl' style='width:100%;'><span class='nameH' style='width:8%;'></span><span class='nameT' v-html='item.hxDeclare'></span></div>
                                </li>
                                <div style="clear:both"></div>
                            </ul>
                             <p  v-html='item.other'></p>
                             <!-- style='color:#333;margin-top:15px;line-height:26px;' -->
                            <!-- <div class='nor' v-html='item.other'></div> -->
                       </div>
                   </div>
                   <div  class='shadow' >
                            <!--  -->
                            <span id='huxing' style='position:relative;top:-120px;opacity:0;font-size:10px;' class='nav2'>1</span>
                            <ul class='detailUl'>
                                <li class='fenLi' style='border:none;'>
                                    <p class='tit'></p>
                                     <ul class='huxUl'>
                                         <li class='huLi' v-for='(z,index) in buildlist' v-show='index==0'>
                                             <div class='fl houseImg'>
                                                 <img :src='z.httpPath' width='100%' height='100%'/>
                                             </div>
                                             <div class='fl houseMsg'>
                                                 <p class='tit' v-html="z.name"></p>
                                                 <!-- <p class='maiji'>{{item.structurArea}}</p> -->
                                                 <p class='totalP'> <span class='pric'>{{z.amount}}</span><span class='check fr btnHoverZ' @click='toBigImg(z.httpPath)'></span></p>
                                             </div>
                                             <div style='clear:both;'></div>
                                         </li>
                                     </ul>
                                </li>
                            </ul>
                    </div>
                    <div  class='shadow' >
                        <!--  -->
                        <span id='zhoubian' style='position:relative;top:-120px;opacity:0;font-size:10px;' class='nav3'>1</span>
                        <ul class='detailUl'>
                            <li class='fenLi'>
                                <p class='tit'></p>
                                <div v-html='item.circunDeclare' class='nor'></div>
                            </li>
                        </ul>
                    </div>
                    <div  class='shadow' >
                        <!--  -->
                         <span id='goufang' style='position:relative;top:-120px;opacity:0;font-size:10px;' class='nav4'>1</span>
                        <ul class='detailUl'>
                            <li class='fenLi'>
                                <p class='tit'></p>
                                <div v-html='item.gfDeclare' class='nor'></div>
                            </li>
                        </ul>
                    </div>
                    <div id='' class='shadow' v-if='itdFileNum>0'>
                        <!--  -->
                        <span id='file' style='position:relative;top:-120px;opacity: 0;font-size:10px;' class='nav5'>1</span>
                        <ul class='detailUl'>
                            <li class='fenLi' v-for="(item,index) in itdFileList" v-show='index==0'>
                                <p class='tit'></p>
                                <img src='./img/file.png' class='fileImg'/>
                                <p class='fileTit'>{{item.fileName}}</p>
                                <a :href='item.filePath'><button class='fileBtn'></button></a>
                                <p class='fileText'></p>
                            </li>
                        </ul>
                    </div>
               </div>
           </div>
        </div>
        <sidebar></sidebar>
        <appointment ref='c1'></appointment>
        <f></f>
    </div>
</template>
<script>
import h from '@/components/header/header.vue'
import f from '@/components/footer/footer.vue'
import sidebar from '../../components/sidebar/sidebar.vue'
import appointment from '../../components/appointment/appointment.vue'
import axios from 'axios'
import { setTimeout } from 'timers';
export default {
    name:'haiWdet',
    metaInfo: function(){
        return{
            title: this.metaItemName+'__',
      meta: [{                 // set meta
        name: 'description',
        content: ''
      },{                 // set meta
        name: 'keyWords',
        content: ''
      }]
        }
        
    },
    data:function(){
        return{
            proId:'',//Id
            nationCode:'',//code
            name:'',
            itemInfoArr:[],
            fileList:[],
            buildlist:'',
           // bigSrclist:'',
            vCover:false,
            ifShow:true,
            itdFileNum:'',
            itdFileList:'',
            loading:this.$loading({
                lock: true,
                text: '...',
                spinner: 'el-icon-loading loading',
                background:'rgba(0, 0, 0, 0.7)',
                }),
           metaItemName:'',
        }
    },
    components:{sidebar,appointment,f,h},
    methods:{
        getInfo:function(id){
            var that=this;
            var obj={"FLAG":"3","ITEM_ID":id}
            axios.post('/trpch/trcrm/item/queryItemInfo',obj)
            .then(res=>{
                var data=res.data;
                console.log(data);
                if(data.isSuccess==1){
                   var itemInfo=data.itemInfo;
                   that.metaItemName=itemInfo.itemTitle;
                   that.itemInfoArr.push(itemInfo);
                    that.fileList=data.fileList;
                    that.buildlist=data.buildlist;
                   that.proId=itemInfo.proId;
                   that.nationCode=itemInfo.nationCode;
                   that.name=itemInfo.itemTitle;
                    //
                    that.itdFileNum=data.itdFileNum;
                    that.itdFileList=data.itdFileList;

                   
                   that.loading.close();
                   setTimeout(function(){
                       var detH=$('.posiRle').height()-250;
                       $('.yiminDetBox').height(detH);
                   },500)
                }     
            })
        },
        subTime:function(str){
            return str.substr(0,10);
        },
        toBigImg:function(){
            var that=this;
            that.vCover=true;
             //that.bigSrc=src;
        },
        closeCover:function(){
             var that=this;
            that.vCover=false;
        },
        toAppont:function(){
            var that=this;
            that.$refs.c1.toShow(that.name,that.id,that.ifShow,true,that.nationCode);
        }
    },
    created:function(){
        var that=this;
        that.id=that.$route.query.id;
        if(!that.id==false){
           that.getInfo(that.id)//
        }
    }
}
</script>
<style>
    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }
    
    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
    .cover{
        width:100%;
        height:100%;
        background-color: rgba(0,0,0,0.4);
        position: fixed;
        z-index:1001;
    }
    .cover img{
        display: block;
        width:50%;
        position: relative;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);
    }
    #haiWB{
        background-image: url(img/haiWbanner.png);
    }
</style>




