<template>
    <div class='yimin' >
        <h1 style="display:none!important;">{{metaH1}}</h1>
        <h></h>
        <div class='yiminMain'>
            <div class='ban' id='liuxueB'>
                <span></span>
            </div>
           <div class='yiminListBox shadow'>
               <div class='fl navT'>
                   <ul class="NaVul">
                       <li class='active' @click="navClick('all',$event)" countryId=''><span></span><img src='./img/fxL.png'/></li>
                       <li  @click="navClick(index,$event)" v-for='(item,index) in countryList' :countryId='item.countryId' name='123'><span>{{item.countryName}}</span><img src='./img/fxL.png'/></li>
                       
                   </ul>
               </div>
               <div class='navList fl'>
                   <div class='noData1' v-if="noData">
                        <img src='./img/no1.png'/>
                        <p></p>     
                    </div>
                   <ul class='navListUl' v-else='noData'>
                       <!-- <div class='cityTop' v-show='cityShow'>
                            <span class='city active' @click="cityClick('',$event)"></span>
                            <span class='city' v-for='(item,index) in cityList' @click="cityClick(item.city,$event)">{{item.city}}</span>
                        </div> -->
                         <li class='navListLi' v-for="item in list">
                            <div class='fl navLiImgB'>
                                <img :src='item.httpPath' @click="checkDetile(item.itemId)" class='pointer'/>
                            </div>
                            <div class='fl dataList yindataList'>
                                <p class='name pointer' @click="checkDetile(item.itemId)">{{item.itemTitle}}</p>
                                <p class='proc deletPic'  style='margin-bottom:61px!important;'>{{item.lightspot}}</p>
                                <p class='type'><i></i>{{item.target}}</p>
                                <p class='type'><i></i>{{item.cycle}}</p>
                            </div>
                            <div class='yiTatol'>
                                     <p style='font-size:36px;color:rgba(176,154,125,1);margin-bottom:10px;'>{{item.amountMaxSum}}<span style='font-size:20px;'></span></p>
                                     <p style='font-size:14px;color:rgba(102,102,102,1);margin-bottom:81px;'> {{item.foreignCurrency}}</p>
                                     <span class='checkDet btnHoverZ' :itemId='item.itemId' @click="checkDetile(item.itemId)"></span>
                            </div>
                        </li>
                        <div style='clear:both;'></div>
                         <el-pagination
                            background
                            layout="prev, pager, next"
                            :total="totalCount"
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="pageNum"
                            :page-size="pageSize"
                            next-text=""
                            prev-text="">
                        </el-pagination> 
                       
                   </ul>
                  
                  
               </div><!--navList-->
               <div style='clear:both;'></div>
           </div>
        </div>
        <sidebar></sidebar>
        <appointment></appointment>
        <f></f>
    </div>
</template>
<script>
import h from '@/components/header/header.vue'
import f from '@/components/footer/footer.vue'
import sidebar from '../../components/sidebar/sidebar.vue'
import appointment from '../../components/appointment/appointment.vue'
import axios from 'axios'
 import { Loading } from 'element-ui';
export default {
    name:'liuxue',
    metaInfo:function() {
        return{
            title: this.metaTitle,
            meta: [{                 // set meta
                name: 'description',
                content: ''
            },{                 // set meta
                name: 'keyWords',
                content: this.keyWords//''
            }]
        }
        
    },
    data:function(){
        return{
            pageSize:10,
            pageNum:1,
            totalCount:1,
            list:'',//
           countryList:[],
            cityList:[],
            cityShow:false,
            thiscity:'',
            countryid:'',
            noData:false,//
            keyWords:'',
            metaH1:'',
            metaTitle:'',
            //countryName:'',//meta
            loading:this.$loading({
                lock: true,
                text: '...',
                spinner: 'el-icon-loading loading',
                background:'rgba(0, 0, 0, 0.7)',
                }),
            options:{
                lock: true,
                text: '...',
                spinner: 'el-icon-loading loading',
                background:'rgba(0, 0, 0, 0.7)',
            }
        }
    },
    components:{sidebar,appointment,f,h},
    methods:{
        handleSizeChange: function (size) {
            this.pageSize = size;
            //this.getData()
            },
        handleCurrentChange: function(currentPage){
            var that=this;
        this.pageNum = currentPage;
        var countryid=that.countryid;
        that.getlist(countryid,this.pageNum);
        
        },
        getCountry:function(){
            var that=this;
            var obj={"BEGIN_NUM":"","CHANNEL":"1","END_NUM":"","TYPE":"2"}
            axios.post('/trpch/trcrm/country/queryCountryList',obj)
            .then(res=>{
                var data=res.data;
                //console.log(data)
                if(data.isSuccess==1){
                    that.countryList=data.list;
                    
                }     
            })
        },
        getCity:function(id){
             var that=this;
            var obj={"COUNTRY_ID":id,"CHANNEL":"1"};
            axios.post('/trpch/trcrm/country/queryCityList',obj)
                .then(res=>{
                    var data=res.data;
                    //console.log(data)
                    if(data.isSuccess==1){
                        that.cityList=data.list;
                    }     
                })
        },
        getlist:function(id,pageNo){
             var that=this;
              that.loading=that.$loading(that.options);
                var obj={"channel":"1","flag":"2","country":id,"pageNo":pageNo,"pageSize":that.pageSize}
                axios.post('/trpch/trcrm/item/queryItemList',obj)
                .then(res=>{
                    var data=res.data;
                    if(data.isSuccess==1){
                        that.loading.close();
                        that.list=data.itemList;
                        console.log(that.list);
                       
                       if(that.list.length==0){
                           that.totalCount=1;
                           that.noData=true;
                           that.pageNum=1;
                       }else{
                           that.totalCount=data.totalNum;
                           if(that.totalCount>10){
                               $('.el-pagination').css('display','block');
                           }else{
                               $('.el-pagination').css('display','none');
                           }
                          that.noData=false;
                       }
                    //    setTimeout(function(){
                    //        $('.navT').height($('.navList').height()+71);
                    //    },300) 
                    setTimeout(function(){
                        var navH=$('.navT').height();
                           var listH=$('.navListUl').height()+70
                           console.log($('.navListUl').height())
                         $('.yiminListBox').height($('.navListUl').height()+70);
                        //    var navH=$('.navT').height();
                        //    var listH=$('.navList').height()
                        //   // alert(navH<(listH+70))
                        //    if(navH<(listH+70)){
                        //         $('.navT').height($('.navList').height()+71);
                        //    }else{
                        //        $('.navList').height(navH-70)
                        //    }
                           
                       },300) 
                        
                    }     
                })
        },
        navClick:function(index,event){
            var that=this;
            var el = event.currentTarget;
             var countryName=el.getElementsByTagName('span')[0].innerHTML;
            
            if(countryName==''){
                that.metaH1='';
                that.keyWords='';
                that.metaTitle='_'
            }else{
                 that.metaTitle=countryName+''+countryName+''+countryName+'_'
                that.metaH1=countryName+''+countryName+''+countryName+'';
                that.keyWords=countryName+''+countryName+''+countryName+'';
            }


            var countryid=el.getAttribute('countryid');
            $('.NaVul li').removeClass('active');
            el.className='active';
            
            if(index=='all'){
                that.countryid=''
                that.cityShow=false;
                that.getlist('',1);
                return;
            }else{
                that.countryid=countryid;
                //that.getCity(countryid);
                that.getlist(countryid,1)
                that.cityShow=true;
                
            }
        },
        cityClick:function(city,event){
             var that=this;
             var el = event.currentTarget;
             that.thiscity=city;
             $('.cityTop span').removeClass('active');
             el.className=el.className+' active'
            var countryid=that.countryid;
            that.getlist(countryid,1)
        },
        checkDetile:function(id){
             var that=this;
             window.open(that.Host+'liuxuedet?id='+id)
           // window.location=that.Host+'liuxuedet?id='+id;
        }
    },
    created:function(){
        var that=this;
        that.getCountry()//
        that.getlist('',1)//
    }
}
</script>
<style>
 #liuxueB{
        background-image: url(img/liuxuebanner.png);
    }
</style>




