import axios from 'axios';
export default {
    install:function(Vue, options) {
        //Vue.prototype.Host='http://172.16.8.64:8080/?#/';
        Vue.prototype.Host='http://172.16.6.68/';
       // Vue.prototype.Host='http://www.tangrenvisa.com/';
        Vue.prototype.formatDuring = function (mss) {//
            var days = parseInt(mss / (1000 * 60 * 60 * 24));
            var hours = parseInt((mss % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
            var minutes = parseInt((mss % (1000 * 60 * 60)) / (1000 * 60));
            var seconds = (mss % (1000 * 60)) / 1000;
            return days + "  " + hours + "  " + minutes + "  " + Math.round(seconds) + "  ";
        }
        
        Vue.prototype.money = function (value) {//Vue
            if(!value) return '0.00';
            var intPart = parseInt(value); //
            var intPartFormat = intPart.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,'); //
            var floatPart = ".00"; //
            var value2Array = value.split(".");

            //=2
            if(value2Array.length == 2) {
                floatPart = value2Array[1].toString(); //

                if(floatPart.length == 1) { //0,
                    return intPartFormat + "." + floatPart + '0';
                } else {
                    return intPartFormat + "." + floatPart;
                }

            } else {
                return intPartFormat + floatPart;
            }

        }//

        Vue.prototype.trafficStatistics = function (code) {
            axios({
                method:'get',
                url:'/wxservice/wxexternal/trafficStatistics',//
                params: {
                    code: code
                }
            })
            .then(function(res) {
               console.log(res) 
            })//

        }
        
}
}



